/* ----------------------------------------------------------------
	Timeline Styles
-----------------------------------------------------------------*/


.timeline {
	.events-wrapper {
		overflow: hidden;
		
		.events {
			.slick-track {
				position: relative;

				&::before {
					content: "";
					background-color: $theme-color;
					position: absolute;
					left: 4.25rem;
					top: 11.625rem;
					width: calc(100% - 8.5rem);
					height: 3px; 
				}
			}

			.event {
				padding: 0.9375rem 4.25rem 0;
				position: relative;

				figure {
					img {
						// width: 18.75rem;
						width: 100%;
						height: 23.4375rem;
						object-fit: cover;
						object-position: top;

						@include grayscale(1);
						@include transition(all 0.3s ease-in-out);
					}

					figcaption {
						padding-top: $spacer * 2.8125;
					}
				}

				.event-date {
					color: $white;
					font-family: $third-font;
					font-size: 3rem;
					line-height: 1;
					position: absolute;
					left: 5.625rem;
					top: 0.5625rem;
					
					@include transition(all 0.3s ease-in-out);
					@include rotate(-20deg);
				}

				&:hover {
					figure {
						img {
							@include grayscale(0);
						}
					}

					.event-date {
						color: $theme-color;
					}
				}
			}
		}
	}
}